import express from 'express';
import expressJWT from 'express-jwt';
import env from '../../../config/env';
import getLoggedInUser from '../../../middlewares/get_loggedIn_user';
import getProductionDetails from '../controllers/production_details_get';
import { baseUrl } from '../baseurl';

const router = express.Router();

/**
 * @swagger
 * /production:
 *   get:
 *     tags:
 *       - Production
 *     description: Returns list of Production
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: "query"
 *         name: "search"
 *         description: "Search text"
 *         schema:
 *           type: object
 *           properties:
 *              search:
 *                  type: string
 *       - in: "query"
 *         name: "limit"
 *         description: "Sets the number of objects to be returned"
 *         schema:
 *           type: object
 *           properties:
 *              limit:
 *                  type: number
 *       - in: "query"
 *         name: "offset"
 *         description: "Sets the offset to the list of breakages"
 *         schema:
 *           type: object
 *           properties:
 *              offset:
 *                  type: number
 *       - in: "query"
 *         name: "from"
 *         description: "from date"
 *         schema:
 *           type: object
 *           properties:
 *              from:
 *                  type: date
 *       - in: "query"
 *         name: "to"
 *         description: "to date"
 *         schema:
 *           type: object
 *           properties:
 *              from:
 *                  type: date
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *          type: array
 *          items:
 *           type: object
 *           properties:
 *            _id:
 *              type: string
 *            productionType:
 *              type: string
 *            batchNumber:
 *              type: string
 *            batchQuantity:
 *              type: Number
 *            packType:
 *              type: string
 *            healStartCode:
 *              type: string
 *            bottles:
 *              type: Number
 *            cases:
 *              type: Number
 *            status:
 *              type: string
 *            productName:
 *              type: string
 *            productCode:
 *              type: string
 *            lineName:
 *              type: string
 */

/**
 * @swagger
 * /production:
 *   post:
 *     tags:
 *       - Production
 *     description: Returns success message if Production details are added
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: "body"
 *         name: "body"
 *         description: "Required details"
 *         required: true
 *         schema:
 *           type: object
 *           properties:
 *            productionType:
 *              type: string
 *            batchNumber:
 *              type: string
 *            batchQuantity:
 *              type: Number
 *            packType:
 *              type: string
 *            healStartCode:
 *              type: string
 *            bottles:
 *              type: Number
 *            cases:
 *              type: Number
 *            status:
 *              type: string
 *            product:
 *              type: string
 *            line:
 *              type: string
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *           type: object
 *           properties:
 *            _id:
 *              type: string
 *            productionType:
 *              type: string
 *            batchNumber:
 *              type: string
 *            batchQuantity:
 *              type: Number
 *            packType:
 *              type: string
 *            healStartCode:
 *              type: string
 *            bottles:
 *              type: Number
 *            cases:
 *              type: Number
 *            status:
 *              type: string
 *            product:
 *              type: string
 *            line:
 *              type: string
 */

/**
 * @swagger
 * /production:
 *   put:
 *     tags:
 *       - Production
 *     description: Returns success message if Production details are updated
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: "body"
 *         name: "body"
 *         description: "Required details"
 *         required: true
 *         schema:
 *          type: array
 *          items:
 *            type: object
 *            properties:
 *              _id:
 *                type: string
 *              update:
 *                type: object
 *                properties:
 *                  productionType:
 *                    type: string
 *                  batchNumber:
 *                    type: string
 *                  batchQuantity:
 *                    type: Number
 *                  packType:
 *                    type: string
 *                  healStartCode:
 *                    type: string
 *                  bottles:
 *                    type: Number
 *                  cases:
 *                    type: Number
 *                  status:
 *                    type: string
 *                  product:
 *                    type: string
 *                  line:
 *                    type: string
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *           type: object
 *           properties:
 *            message:
 *              type: string
 */
router.get(
  `${baseUrl}/`,
  expressJWT({
    secret: env.JWT_SECRET,
  }),
  getLoggedInUser,
  getProductionDetails
);

export default router;
